




import Vue from "vue";

export default Vue.extend({
  props: {
    status: String
  },
  computed: {
    getClass(): string {
      switch (this.status) {
        case "green":
          return "light green";
        case "amber":
          return "light amber";
        case "red":
          return "light red";
        default:
          return "light";
      }
    }
  }
});
